.gardenItem {
  display: flex;
  height: 30vh;
  width: 65vw;
  margin-bottom: 10px;
  align-items: center;
  padding: 10px;
  overflow-y: hidden;
  position: relative;
  margin: 3em auto;
}

.gardenItem-header {
  /* border: 2px solid red; */

  height: 20%;
  width: 99%;
  top: 0;
  position: absolute;
  left: 0;
}

.gardenItem-images {
  /* border: 2px solid blue; */

  overflow-x: scroll;
  overflow-y: hidden;
  height: 60%;
  width: 99%;
  position: absolute;
  bottom: 1.5em;
  left: 0;
  display: inline-flex;
  padding-bottom: 1em;
}

.gardenItem-shelf {
  /* border: 2px solid red; */

  height: 5%;
  width: 99%;
  bottom: 0;
  position: absolute;
  left: 0;

  border-bottom: 1em solid #ead2a7;
  border-radius: 0px 0px 11px 10px;
}

.skew-left {
  z-index: -10;
  background-color: moccasin;
  height: 5em;
  bottom: 0;
  position: absolute;
  width: 50%;
  left: 1.8em;
  transform: skewX(-37deg);
}

.skew-right {
  z-index: -10;
  background-color: moccasin;
  height: 5em;
  bottom: 0;
  position: absolute;
  width: 50%;
  right: 1.8em;
  transform: skewX(37deg);
}

.burger-menu-gardenItem {
  /* border: 2px solid black; */

  height: 100%;
  width: 10%;
  top: 0;
  position: absolute;
  right: 0;
}

.water-gardenItem {
  /* border: 2px solid black; */

  height: 100%;
  width: 10%;
  top: 0;
  position: absolute;
  left: 0;

  cursor: pointer;
}

.plant-name-gardenItem {
  /* border: 2px solid black; */

  height: 100%;
  width: 40%;
  top: 0;
  position: absolute;
  left: 30%;
}

.display-plant-name {
  top: 0;
  position: absolute;
  width: 100%;
  left: 0;
  /* border-bottom: 2px solid; */
}

.gardenPlantIndex .deleteButton {
  bottom: 0;
  position: absolute;
  right: 0;
  margin: 1em 1em;
}
.gardenPlantIndex .editButton {
  bottom: 0;
  position: absolute;
  left: 0;
  margin: 1em 1em;
}

img.myGardenPlantPhoto {
  position: relative;
  height: 80%;
  margin: 2em 2em 0 2em;
  border-radius: 2em;
  border: 2px solid #bfb29c;
  box-shadow: 1px 9px 7px -4px rgba(0, 0, 0, 0.47);

  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
}

.image-box {
  position: relative;
  height: 95%;
  border-radius: 2em;
  z-index: 2;
  top: 0;
  background-color: #ffffff00;
  transition: all 0.2s ease-in-out;
  -webkit-transition: all 0.2s ease-in-out;
}

.plant-details {
  position: relative;
  width: 100%;
  text-align: center;
  bottom: 3em;
  font-size: 1em;
  font-weight: 700;
  color: #464646;
  z-index: -1;
}
